import axios from 'axios';
import type { Order, CreateOrderRequest, Payment, InventoryItem, DashboardMetrics } from '@/types';

const API_BASE_URL = (import.meta as any).env.VITE_API_URL || 'http://localhost:8080';

export const apiClient = axios.create({
  baseURL: API_BASE_URL,
  timeout: 10000,
  headers: {
    'Content-Type': 'application/json',
  },
});

// Add correlation ID to requests
apiClient.interceptors.request.use((config) => {
  config.headers['X-Correlation-ID'] = `req-${Date.now()}-${Math.random().toString(36).substr(2, 9)}`;
  return config;
});

// Response interceptor for error handling
apiClient.interceptors.response.use(
  (response) => response,
  (error) => {
    console.error('API Error:', error.response?.data || error.message);
    return Promise.reject(error);
  }
);

// Extended API client with typed methods
const api = Object.assign(apiClient, {
  // Dashboard methods
  getDashboardMetrics: () => apiClient.get<DashboardMetrics>('/api/dashboard/metrics'),
  
  // Order methods
  getOrders: () => apiClient.get<Order[]>('/api/orders'),
  getOrder: (id: string) => apiClient.get<Order>(`/api/orders/${id}`),
  createOrder: (order: CreateOrderRequest) => apiClient.post<Order>('/api/orders', order),
  cancelOrder: (id: string) => apiClient.delete(`/api/orders/${id}`),
  
  // Payment methods
  getPayments: () => apiClient.get<Payment[]>('/api/payments'),
  getPayment: (id: string) => apiClient.get<Payment>(`/api/payments/${id}`),
  retryPayment: (id: string) => apiClient.post(`/api/payments/${id}/retry`),
  
  // Inventory methods
  getInventory: () => apiClient.get<InventoryItem[]>('/api/inventory'),
  
  // Health checks
  getOrderHealth: () => apiClient.get('/api/orders/health'),
  getPaymentHealth: () => apiClient.get('/api/payments/health'),
  getInventoryHealth: () => apiClient.get('/api/inventory/health'),
});

export default api;